body{
    background: #E6E6E6
}
body.about .box{
display: none;
}
.listing-posts{
    display: none;
}
.site-header{
    background: #252525;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0.5em 0;
}
.site-title a{
    text-decoration: none;
    font-size: 2em;
    padding-left: 0.5em;
}
.site-description{
    padding: 0;
    color:#FFFFFF;
    font-weight: bold;
}
.site-header span{
    color:white;
    font-family: 'Source Code Pro', monospace;
}
.site-header span:last-of-type{
    color:red;
    font-family: 'Source Code Pro', monospace;
}

.welcome-image{
    max-width: 80%;
    height: 70vh;
    background-image: url('https://populous.com/wp-content/uploads/1219_TOTTENHAM_UPDATES_VIEW_01_UPPER_SEATS_REV_O1.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    margin:0 auto;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.welcome .welcome-image h2{
    color:white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.welcome .welcome-image p{
    border:1px solid black;
    color:white;
    font-weight:bold;
    max-width: 50%;
    text-align: center;
    padding-bottom: 0;
    margin-top: 2em;
    background: black;
    opacity: 0.5;
}


